* {
  box-sizing: border-box;
  margin: 0;
}

.body {
  color: #060b35;
  font-family: Roboto, sans-serif;
  font-size: 12px;
}

.header {
  width: 100%;
  max-width: 1920px;
  padding: 0 50px;
  box-shadow: 0 2px 4px #0000000d;
}

.header__container {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  max-width: 1200px;
  margin: 0 auto;
  display: flex;
}

.header__logo {
  display: flex;
}

.nav__list {
  padding: 0;
  display: flex;
}

.nav__item {
  list-style: none;
}

.nav__item:not(:first-child) {
  margin-left: 20px;
}

.nav__link {
  color: inherit;
  text-transform: uppercase;
  align-items: center;
  height: 60px;
  font-weight: 500;
  text-decoration: none;
  display: flex;
  position: relative;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:after {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
}

.cards-container {
  grid-template-rows: repeat(auto-fit, 408px);
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  margin: 0 auto;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .cards-container {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .cards-container {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .cards-container {
    grid-template-columns: repeat(4, 200px);
  }
}

.card {
  box-sizing: border-box;
  cursor: pointer;
  background-color: #fff;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  display: flex;
}

.card__image {
  width: 160px;
  height: 134px;
  margin-bottom: 40px;
  display: flex;
}

.card__name {
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
}

.card__code {
  color: #616070;
  margin-bottom: 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__reviews {
  justify-content: space-between;
  align-items: center;
  height: 16px;
  margin-bottom: 24px;
  display: flex;
}

.card__count {
  font-size: 10px;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-bottom: 16px;
  display: flex;
}

.card__price--text {
  color: #616070;
  font-size: 12px;
  line-height: 18px;
}

.card__price--number {
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__button {
  color: #fff;
  text-transform: uppercase;
  cursor: pointer;
  background: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  width: 166px;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
}

.card__button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover .card__name {
  color: #34568b;
}

.stars {
  display: flex;
}

.stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars--4 :nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}
